* {
	font-family: Arial, Helvetica, sans-serif;
}

body {
	padding: 60px;
}

input {
	color: gray;
	width: 100%;
	font-size: 1.1rem;
}

.card {
	align-content: center;
	border: 1px solid gray;
	border-radius: 6px;
	overflow: hidden;
	margin-bottom: 10px;
	overflow: hidden;
}

.card p {
	padding: 5px;
	text-align: center;
}

.cardGrid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}